.panel-prom-section{
    background-color: #1A2333;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    height: auto;
    min-height: 100vh;
}

.panel-bg-image{
    position: absolute;
    top: 10rem;
    right: 10rem;
    object-fit: cover;
    object-position: center;
    scale: 0.8rem;
    opacity: 0.8;
}

.panel-content{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    padding: 14rem 0;
    z-index: 99;
    margin-left: 30rem;
}

.panel-image{
    scale: 0.8;
    margin-bottom: 1.5rem;

}

.panel-feature-container{
    margin-left: 15rem;
    display: flex;
    flex-direction: column; 
    margin-top: 0rem;       
}