.credit-section {
    background-color: #1A2333;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: auto;
    min-height: 100vh;
}

.gold-image {
    position: absolute;
    bottom:9rem;
    right: 9rem;
    object-fit: cover;
    object-position: center;
}

.credit-image {
    scale: 0.8;
    margin-left: -10rem;
}

.credit-bg-image{
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
    object-position: center;
}

.credit-content{
    display: flex;
    flex-direction: column;

    align-items: center;
    width: 100%;
    padding: 14rem 0;
    z-index: 99;
    margin-left: -25rem;
}
.credit-feature-container{
    margin-left: 23rem;
    display: flex;
    flex-direction: column; 
    
    margin-top: 0rem;
       
}

.credit-feature{
    font-size: 3.3rem;
    color: #fff;
    margin-bottom: 0rem;
}

.text-container {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    margin-bottom: 4.4rem;
    height: auto;
}

.credit-icon{
    font-size: 3.8rem;
    margin-right: 2rem;
    color: #fff;
}